import faker from 'faker';
import { capitalize } from 'lodash';

const columnNames = [
  {
    name: 'urgency',
    title: 'Urgency',
  },
  {
    name: 'status',
    title: 'Status',
  },
  {
    name: 'ticket_id',
    title: 'Ticket ID',
  },
  {
    name: 'dueDate',
    title: 'Due Date',
  },
  {
    name: 'type',
    title: 'Type',
  },
  {
    name: 'specificType',
    title: 'Specific Type',
  },
  {
    name: 'member',
    title: 'Member',
  },
  {
    name: 'company',
    title: 'Company',
  },
  {
    name: 'ticketOwner',
    title: 'Ticket Owner',
  },
  {
    name: 'assignedTo',
    title: 'Assigned To',
  },
  {
    name: 'createdDate',
    title: 'Created Date',
  },
  {
    name: 'lastUpdate',
    title: 'Last Update',
  },
  {
    name: 'channel',
    title: 'Channel',
  },
  {
    name: 'is_vip',
    title: 'VIP',
  },
];

const getColumnExtensions = () => {
  const columns: any[] = [];

  columnNames.map(column => {
    let width = 200;

    switch (column.name) {
      case 'urgency':
      case 'status':
      case 'app_uid':
        width = 100;
        break;
      case 'type':
      case 'dueDate':
        width = 120;
        break;
      case 'specificType':
      case 'company':
        width = 250;
        break;
      default:
        width = 200;
    }

    columns.push({
      columnName: column.name,
      width: width,
      wordWrapEnabled: true,
    });
  });

  return columns;
};

const generateRows = () => {
  const rows: any[] = [];
  const types = ['Inquiry', 'Availment', 'Request', 'Complaint'];
  const specTypes = [
    'Billing Dispute Inquiry',
    'Outpatient Consult',
    'X-Ray Procedure Availment',
    'Service Complaint',
    'Dental Coverage',
    'Premium Adjustment',
  ];
  const statuses = ['open', 'pending', 'resolved', 'closed'];
  const channels = ['email', 'fax', 'sms'];
  const is_vip = [true, false];
  for (let index = 0; index < 50; index++) {
    const status = faker.random.arrayElement(statuses);
    const urgency =
      status === 'closed' || status === 'resolved'
        ? 3
        : faker.random.arrayElement([1, 2]);

    rows.push({
      urgency: urgency,
      status: capitalize(status),
      app_uid: faker.random.alphaNumeric(8),
      dueDate: faker.date.future(),
      type: faker.random.arrayElement(types),
      specificType: faker.random.arrayElement(specTypes),
      member: faker.name.findName(),
      company: faker.company.companyName(),
      ticketOwner: faker.name.findName(),
      assignedTo: faker.name.findName(),
      createdDate: faker.date.future(),
      lastUpdate: faker.date.future(),
      channel: faker.random.arrayElement(channels),
      is_vip: faker.random.arrayElement(is_vip),
    });
  }

  return rows;
};

const tableData: any = {
  formattedColumns: {
    required_columns: [],
    urgency_columns: ['urgency'],
    text_columns: [
      'status',
      'app_uid',
      'type',
      'specificType',
      'member',
      'company',
      'ticketOwner',
      'assignedTo',
      'channel',
    ],
    date_columns: [
      'dueDate', 
      'createdDate', 
      'lastUpdate',
    ],
    boolean_columns: [
      'is_vip',
    ],
    detaillink_columns: [
      'app_uid',
    ]
  },
  columnExtensions: getColumnExtensions(),
  fixedColumns: [
    'urgency',
    'status',
    'dueDate',
    'app_uid',
    'type',
    'specificType',
    'member',
  ],
  cellProperties: [],
  columns: columnNames,
  rows: generateRows(),
};

export { tableData };
