const popoverStyles = makeStyles((theme: Theme) => ({
  popoverCheckbox: {
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
  },
}));

interface SelectColumnsPopoverProps {
  columns: any[];
  selectedColumns: any[];
  setSelectedColumns: any;
}

const SelectColumnsPopover: React.FC<SelectColumnsPopoverProps> = (
	props: SelectColumnsPopoverProps,
): JSX.Element => {
  const { 
    columns,
    selectedColumns,
    setSelectedColumns,
  } = props;
  const classes = popoverStyles(props);

  const onCheckChange = (columnObj: any) => (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    if (event.target.checked) {
      const tmpSelectedColumns = [...selectedColumns];
      tmpSelectedColumns.push(columnObj);

      setSelectedColumns(tmpSelectedColumns);
    } else {
      const tmpSelectedColumns = [...selectedColumns];
      remove(tmpSelectedColumns, columnName => {
        return columnName.name === columnObj.name;
      });

      setSelectedColumns(tmpSelectedColumns);
    }
  };

  const popupState = usePopupState({
    variant: 'popover',
    popupId: 'columnListPopover',
  });

  return(
    <div>
      <Button fullWidth {...bindTrigger(popupState)}>
        <Typography className={clsx('link-button')} align="right">
          <FontAwesomeIcon
            icon={faThList}
            className={clsx('link-button-icon')}
          />
          <span className={clsx('link-button-text')}>Display Columns</span>
        </Typography>
      </Button>
      <Popover
        {...bindPopover(popupState)}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
      >
        <FormGroup>
          {map(columns, (column, idx) => {
            const checked =
              findIndex(selectedColumns, { name: column.name }) >= 0;

            return (
              <FormControlLabel
                key={idx}
                control={
                  <Checkbox
                    checked={checked}
                    onChange={onCheckChange(column)}
                    value={column.name}
                    color="primary"
                  />
                }
                className={classes.popoverCheckbox}
                label={column.title}
              />
            );
          })}
        </FormGroup>
      </Popover>
    </div> 
  );
};





export class TicketListPage extends React.Component<ITicketListPage.IProps, ITicketListPage.IState> {

  tickets: any[];
  tableListRef: any;
  
  constructor(props: ITicketListPage.IProps) {
		super(props);

		this.state = {
      loading: false,
      status: '',
      query: '',
      columns: [],
      selectedColumns: [],
      fixedColumns: [],
      formattedColumns: {},
      columnExtensions: [],
      rows: [],
      ticketStats: {},
      ticketList: [],

      filters: {}
    };

    this.tickets = [];
    this.tableListRef = React.createRef();
    //localStorage.setItem('pm_token', '2f0612ef209de4a941580145912ef252549a343d');
  }
  
  onLogout = () => {
    window.location.replace('../index.html#/');
  }

	reset = () => {
		this.setState({
      loading: false,
      // status: '',
      query: '',
      // columns: [],
      // selectedColumns: [],
      // fixedColumns: [],
      // formattedColumns: {},
      // columnExtensions: [],
      // rows: [],
      // ticketStats: {},
      // ticketList: [],
		});
  };

  getTicketStats = (tickets: any[]) => {
    this.setState({ticketStats: {
      all: tickets.length,
      open: tickets.filter(ticket => ticket.status.toLowerCase() === 'open').length,
      closed: tickets.filter(ticket => ticket.status.toLowerCase() === 'closed').length,
      resolved: tickets.filter(ticket => ticket.status.toLowerCase() === 'resolved').length,
      pending: tickets.filter(ticket => ticket.status.toLowerCase() === 'pending').length
    }})
  };

  loadTicketList = async () => {

    if (this.state.loading) {
      return;
    }

    this.setState({loading: true});

    const filter = {
      where: { '$and': [{ ticket_id: { '$ne': null }}] }
    };

    API.get(`customer-care/tickets?filter=${JSON.stringify(filter)}`,
      (tickets) => {
        console.log(tickets);
        tickets.map(ticket => {
          this.tickets.unshift({
            urgency: ticket.ticket_category.urgency,
            status: ticket.status,
            _id: ticket._id,
            ticket_id: ticket.ticket_id,
            caseId: ticket.pmaker_case_uid,
            dueDate: '',
            type: ticket.ticket_category.ticket_type,
            specificType: ticket.ticket_category.specific_type,
            member: ticket.member_data ? 
              GlobalFunction.toNameFormat(
                ticket.member_data.first_name,
                ticket.member_data.middle_name,
                ticket.member_data.last_name
              ) : 'N/A',
            company: 
              ticket.member_data ? ticket.member_data.client : 
              ticket.client_data ? ticket.client_data.registered_name : 
              'N/A',
            is_vip: ticket.member_data ? ticket.member_data.is_vip ? 'Yes' : 'No' : 'N/A',
            ticketOwner: ticket.created_by.name,
            assignedTo: ticket.assign_to,
            createdDate: GlobalFunction.toDateAndTime(new Date(ticket.date_created)),
            lastUpdate: GlobalFunction.toDateAndTime(new Date(ticket.date_updated)),
            channel: ticket.received_via,
            transaction_no: ticket.transaction_no,
          });
        });

        this.getTicketStats(this.tickets);
        this.setState({loading: false});
      },
      (error) => {
        console.log(error);
      }
    );
  }

  setSelectedColumns = (cols) => {
    this.setState({
      selectedColumns: cols
    });
  }

  getVisibleColumns = () => {
    return filter(this.state.columns, column => {
      return findIndex(this.state.selectedColumns, { name: column.name }) >= 0;
    });
  }

  generateReport = () => {
    console.log('generate report');
  }

  onSearchTextChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    const { value } = e.target;
    this.setState({query: value});
  }

  onStatusFilterClick = (newStatus) => {
    if(this.state.status === newStatus){
      return;
    }
    //console.log('status: ', newStatus);
    this.setState({status: newStatus});

    this.tableListRef.current.resetPage();
  }

  componentWillMount = () => {
    let cols = tableData.columns;
    this.setState({
      columns: cols,
      selectedColumns: cols,
      fixedColumns: (Object.keys(tableData).length > 0 ? tableData['fixedColumns'] : []),
      formattedColumns: (Object.keys(tableData).length > 0 ? tableData['formattedColumns'] : []),
      columnExtensions: (Object.keys(tableData).length > 0 ? tableData['columnExtensions'] : []),
    });
  }

  componentDidMount = () => {
    this.loadTicketList();
  }

  getFilteredTickets = (columns: any) => {
    let filteredTickets = this.tickets;

    columns.map(column => {
      filteredTickets = filteredTickets
        .filter(ticket => 
          this.state.filters[column.name] ? (
            this.state.filters[column.name] === '' ||
            (ticket[column.name] && 
              ticket[column.name].toString().toLowerCase().includes(this.state.filters[column.name].toLowerCase())
            )
          ) : true
        )
        .filter(ticket => 
          this.state.status === '' || 
          ticket.status.toLowerCase() === this.state.status.toLowerCase())
        .filter(ticket => 
          this.state.query === '' ||
          ticket['ticket_id'].toLowerCase().includes(this.state.query.toLowerCase()));
    });

    if (this.tableListRef.current) {
      this.tableListRef.current.resetPage();
    }

    return filteredTickets;
  }

  sortTickets(attrName: string, isAsc: boolean): void {
    this.tickets.sort((a: any, b: any) => 
        (a[attrName] && b[attrName] && a[attrName].toString().toLowerCase() > b[attrName].toString().toLowerCase()) ? 
          (isAsc ? 1 : -1) : (isAsc ? -1 : 1)
    );
  }

  public render(): JSX.Element {
    let ins  = this;
    
    return (
      <div>
        { this.state.loading ?
          (<Loader/>) : null
        }

        <div style={{position: 'absolute', right: '1rem', top: '1rem'}}>
          <Fab color='primary' style={{backgroundColor: '#CCCCCC'}}>
          <ExitToAppIcon onClick={this.onLogout} />
          </Fab>
        </div>

        <div className={clsx('TicketListPage')}>
          <Grid
            container
            direction="row"
            justify="space-between"
            alignItems="baseline" >
            <Grid item xs={12} className={clsx('header-label')}>
              <Typography className={clsx('title')} color="textPrimary">
                Tickets
              </Typography>
            </Grid>
          </Grid>
          <Grid
            container
            direction="row"
            justify="space-between"
            alignItems="center"
            spacing={1}
            className={clsx('search')} >
            <Grid item sm={12} md={8}>
              <TextField
                id="cc_search_tickets_tf"
                placeholder="Search ticket ID"
                className={clsx('search-field')}
                margin="dense"
                variant="outlined"
                fullWidth
                onChange={ins.onSearchTextChange}
                value={ins.state.query}
                inputProps={{ 'aria-label': 'bare' }}
                InputProps={{
                  startAdornment: (
                    <FontAwesomeIcon
                      icon={faSearch}
                      className={clsx('input-adornment')}
                    />
                  ),
                }}
              />
            </Grid>
            <Grid item sm={6} md={2} alignContent="flex-end" justify="flex-end">
              <Button fullWidth onClick={() => { this.generateReport() }} disabled>
                <Typography className={clsx('link-button disabled')} align="right">
                  <FontAwesomeIcon
                    icon={faDownload}
                    className={clsx('link-button-icon disabled')}
                  />
                  <span className={clsx('link-button-text')}>Generate Report</span>
                </Typography>
              </Button>
            </Grid>
            <Grid item sm={6} md={2} alignContent="flex-end">
              <SelectColumnsPopover 
                setSelectedColumns={this.setSelectedColumns} 
                columns={this.state.columns} 
                selectedColumns={this.state.selectedColumns} />
            </Grid>
          </Grid>
          <Grid container direction="row" className={clsx('tickets-table')}>
            <Grid item xs={12}>
              <Paper className={clsx('tickets-table-container')}>
                <Grid container xs={12}>
                  <Grid item xs className={clsx('tickets-table-stats')}>
                    <Typography align="center" onClick={() => { this.onStatusFilterClick('') }}>
                      <span className={(this.state.status=='') ? clsx('tickets-all') : clsx('tickets-default')}>
                        {this.state.ticketStats.all? this.state.ticketStats.all : 0}
                      </span>
                      <span className={(this.state.status=='') ? clsx('ticket-label-active') : clsx('ticket-label')}>ALL</span>
                    </Typography>
                  </Grid>
                  <Grid item xs className={clsx('tickets-table-stats')}>
                    <Typography align="center" onClick={() => { this.onStatusFilterClick('open') }}>
                      <span className={(this.state.status=='open') ? clsx('tickets-open') : clsx('tickets-default')}>
                        {this.state.ticketStats.open? this.state.ticketStats.open : 0}
                      </span>
                      <span className={(this.state.status=='open') ? clsx('ticket-label-active') : clsx('ticket-label')}>OPEN</span>
                    </Typography>
                  </Grid>
                  <Grid item xs className={clsx('tickets-table-stats')}>
                    <Typography align="center" onClick={() => { this.onStatusFilterClick('pending') }}>
                      <span className={(this.state.status=='pending') ? clsx('tickets-pending') : clsx('tickets-default')}>
                        {this.state.ticketStats.pending? this.state.ticketStats.pending : 0}
                      </span>
                      <span className={(this.state.status=='pending') ? clsx('ticket-label-active') : clsx('ticket-label')}>PENDING</span>
                    </Typography>
                  </Grid>
                  <Grid item xs className={clsx('tickets-table-stats')}>
                    <Typography align="center" onClick={() => { this.onStatusFilterClick('resolved') }}>
                      <span className={(this.state.status=='resolved') ? clsx('tickets-resolved') : clsx('tickets-default')}>
                        {this.state.ticketStats.resolved? this.state.ticketStats.resolved : 0}
                      </span>
                      <span className={(this.state.status=='resolved') ? clsx('ticket-label-active') : clsx('ticket-label')}>RESOLVED</span>
                    </Typography>
                  </Grid>
                  <Grid item xs className={clsx('tickets-table-stats')}>
                    <Typography align="center" onClick={() => { this.onStatusFilterClick('closed') }}>
                      <span className={(this.state.status=='closed') ? clsx('tickets-closed') : clsx('tickets-default')}>
                        {this.state.ticketStats.closed? this.state.ticketStats.closed : 0}
                      </span>
                      <span className={(this.state.status=='closed') ? clsx('ticket-label-active') : clsx('ticket-label')}>CLOSED</span>
                    </Typography>
                  </Grid>
                </Grid>
                <Grid item xs={12} style={{overflow: 'auto'}}>
                  <CustomTable
                    ref={this.tableListRef}
                    className='ticket-list-table'
                    canOverflow
                    rows={this.getFilteredTickets(this.state.columns)}
                    stickyHeader={true}
                    renderHeader={() => this.renderTableHeader(this.state.columns, this.state.fixedColumns)}
                    renderRow={(row: any, index: number) => this.renderTableRow(row, index, this.state.columns, this.state.fixedColumns)}
                    rowsPerPageOptions={[5, 10, 15]} />
                </Grid>
              </Paper>
            </Grid>
          </Grid>
        </div>
      </div>
    );
  }
  
  renderTableHeader(columns: any, fixedColumns: any): JSX.Element {
    let dockedColumnsCounter = 0;
    return (
      <>
      {columns
        .filter(column => {
          for (var i = 0; i < this.state.selectedColumns.length; i++) {
            if (this.state.selectedColumns[i].name === column.name) {
              return true;
            }
          }
          return false;
        })
        .map((column, index) => {
          const matchedColumn = fixedColumns.filter(col => col === column.name)[0];
          let nextColumnIsFixed = true;
          if (matchedColumn && columns[index + 1]) {
            nextColumnIsFixed = fixedColumns.filter(col => col ===  columns[index + 1].name)[0] ? true : false;
          }
          const style = {
            left: matchedColumn ? dockedColumnsCounter * 125 : 0,
            zIndex: matchedColumn ? 125 : 0,
            minWidth: '125px',
            maxWidth: '125px',
            borderRight: !nextColumnIsFixed ? '#9EA3AC 2px solid' : undefined
          };
          if (matchedColumn) {
            dockedColumnsCounter++;
          }
          return (
            <TableCell style={style}>
              <span className='header-2'>{column.title}</span>
              <div style={{float: 'right'}}>
                  <IconButton 
                      icon={faSort} 
                      onClick={() => {
                          this.sortTickets(column.name, true);
                          this.setState({loading: false});
                      }} />
              </div>
              <InputField
                  value={this.state.filters[column.name]}
                  onChange={(value: string) => {
                    this.state.filters[column.name] = value;
                    this.setState({filters: this.state.filters});
                      // this.setState(prevState => ({...prevState, [attrName]: value}));
                  }} />
            </TableCell>
          );
        })
      }
      </>
    );
  }

  renderTableRow(row: any, index: number, columns: any, fixedColumns: any): JSX.Element {
    let dockedColumnsCounter = 0;
    const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
    return (
      <TableRow 
        style={{backgroundColor: backgroundColor}}
        onClick={() => {
          const route = 
            row.status === 'Resolved' || 
            row.specificType === 'Outpatient Procedure' ||
            row.specificType === 'Consultation' ? 
              'resolve-ticket' : 'update-ticket';
              window.location.href = `#/customer-care/${route}/${row.caseId}/${row._id}`;
        }}>
        {columns
          .filter(column => {
            for (var i = 0; i < this.state.selectedColumns.length; i++) {
              if (this.state.selectedColumns[i].name === column.name) {
                return true;
              }
            }
            return false;
          })
          .map((column, index) => {
            const matchedColumn = fixedColumns.filter(col => col === column.name)[0];
            let nextColumnIsFixed = true;
            if (matchedColumn && columns[index + 1]) {
              nextColumnIsFixed = fixedColumns.filter(col => col ===  columns[index + 1].name)[0] ? true : false;
            }
            const style = {
              left: matchedColumn ? dockedColumnsCounter * 125 : 0,
              zIndex: matchedColumn ? 125 : 0,
              position: matchedColumn ? 'sticky' : 'relative' as PositionProperty,
              minWidth: '125px',
              maxWidth: '125px',
              borderRight: !nextColumnIsFixed ? '#9EA3AC 2px solid' : undefined
            };
            if (matchedColumn) {
              dockedColumnsCounter++;
            }
            const urgencyColor = {
              Low: '#3AB77D',
              Medium: '#FAB435',
              High: '#FD5451'
            };
            const urgencyNumber = {
              Low: 3,
              Medium: 2,
              High: 1
            };
            return (
              <TableCell style={style}>
                {column.name === 'urgency' ? (
                  <div style={{width: '100%', textAlign: 'center'}}>
                    <div 
                      className='number-item' 
                      style={{
                        display: 'inline-block', 
                        backgroundColor: urgencyColor[row[column.name]]}}>
                      {urgencyNumber[row[column.name]]}
                    </div>
                  </div>
                ) /*: index == 3 || index == 10 || index == 11 ? (
                  row[column.name]
                ) : column.name === 'urgency' ? (
                  row[column.name] ? 'Yes' : 'No'
                )*/ : row[column.name]}
              </TableCell>
            );
          })
        }
      </TableRow>
    );
  }
}

const mapStateToProps = (state: Store) => state.ticketList;

const mapDispatchToProps = (dispatch: Dispatch) => ({
	Map: bindActionCreators(TicketListActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
